import Vue from 'vue'
import Router from 'vue-router'
import Start from '@/components/Start'
import List from '@/components/pages/ListDocument'
import Card from '@/components/pages/CardDocument'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Start
    },
    {
      path: '/list',
      name: 'List',
      component: List
    },
    {
      path: '/card',
      name: 'Card',
      component: Card
    }
  ],
  mode: 'history'
})



