<template>
    <!-- Модуль карточек Листа согласования -->
    <div class="card ml-2 mb-3 small-tab" style="max-width: 20rem;">
        <div class="card-header">
            <span class="text-muted">Срок:</span> {{cardIn.term}}
            <br>
            <span class="text-muted">Дата согласования:</span> {{cardIn.date}}
        </div>
        <div class="card-body">
            <h5 class="card-title"> {{cardIn.name}}
                <span class="badge badge-pill badge-success float-right">
                    {{cardIn.status}}
                </span>
            </h5>
            <p class="card-text ">
                <b>
                    <div class="row">
                        <div class="font-tab-mid float-left mr-3">
                            <i class="far fa-user-circle fa-3x "></i>
                        </div>
                        <div class="">
                            <span class="text-muted small-tab">
                                {{cardIn.position}}
                            </span>
                            <template v-if="cardIn.messages.length > 0">
                                <div class="row mt-3 font-tab-mid" v-for="(msg, index) in cardIn.messages" :key="index">
                                    <div class="col-12">
                                        <span class="badge badge-pill badge-light float-left ml-3" v-if="index%2 === 0">
                                            {{msg}}
                                        </span>
                                        <span class="badge badge-pill badge-light float-right mr-3" v-else>
                                            {{msg}}
                                        </span>
                                    </div>
                                </div>
                            </template>

                        </div>
                    </div>
                </b>
            </p>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'document-card-log',
    props: [
      'cardIn'
    ]
  }
</script>

<style scoped>
    .font-tab-mid{
        margin-top: 15px;
        font-size: 1.1rem;
    }
    .small-tab{
        font-size: 0.9rem;
    }
    hr{
        border: none;
        background-color: #2a3035;
        color: #2a3035;
        height: 1px;
    }
    .collapse-list ul li{
        color: #455a64;
        list-style-type: none;
        margin-left: -30px;
    }
    .toggle-btn ul li{
        list-style-type: none;
        margin-left: -30px;
    }
    .toggle-btn-line ul li{
        display: inline;
        list-style-type: none;
    }
    .card-header{
        color: #455a64;
        font-weight: bold;
    }
    .c-feedback{
        background-color: aqua;
    }
</style>



