<template>
    <!--Модуль табов списка документов-->
    <b-tabs class="nav nav-tabs tab-content">
            <b-tab class="tab-content" active>
                <template slot="title">
                    <b>Мои задания</b> <i class='fas fa-times-circle'></i>
                </template>
                <div class="row mt-3">
                    <!-- Содержимое вкладки-->
                    <div class="col col-12">
                        <div class="row">
                            <doc-filter></doc-filter>
                        </div>
                        <table-doc :table=tableDoc></table-doc>
                    </div>
                </div>
                <paginator-bot class="pagination-lg justify-content-end" :first-number="true" :count-pages="3"></paginator-bot>
            </b-tab>
            <b-tab>
                <template slot="title">
                    <b>ВСХ ИА-2200</b> <i class='fas fa-times-circle'></i>
                </template>
            </b-tab>
            <b-tab>
                <template slot="title">
                    <b>ИСХ ВР-2200</b> <i class='fas fa-times-circle'></i>
                </template>
            </b-tab>
    </b-tabs>
</template>

<script>
  export default {
    name: 'nav-tabs',
    props: [
      'table'
    ],
    data () {
      return {
        strIcon: false,
        tableDoc: this.table
      }
    },
    methods: {
      iconInstall (index) {
        this.table[index].icon = !this.table[index].icon
      }
    }
  }
</script>

<style scoped>

    .text-fix{
        width: 35%;
    }
    .task-fix{
        display: flex;
        flex-wrap: wrap;
        width: 10%;
    }
</style>



