<template>
    <div class="row">
        <div class="col-12 bg-light border-b">
            <table class="table table-borderless ">
                <tr class="row" v-for="(row, index) in status" :key="index">
                    <td class="col-4 text-muted">{{row.name}}</td>
                    <template v-if="index == 0">
                        <td class="col-8 text-blue" >
                            <b>{{row.description}}</b>
                        </td>
                    </template>
                    <template v-else>
                        <td class="col-8" >
                            <template v-if="row.icon">
                                <div class="row">
                                    <div class="col-3">
                                        <i v-bind:class="row.icon"></i>
                                    </div>
                                    <div class="col-9">
                                        <span class="summary">{{row.description}}</span>
                                        <br>
                                        <span class="text-muted small-tab">{{row.comment}}</span>
                                    </div>
                                </div>
                            </template>
                            <template v-else-if="row.date">
                                <b class="text-blue">{{row.description}}</b>
                                <span class="text-muted">{{row.date}}</span>
                            </template>
                            <template v-else>
                                <b>{{row.description}}</b>
                            </template>
                        </td>
                    </template>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'table-status-doc',
    props: [
      'status'
    ]
  }
</script>

<style scoped>

    .text-blue{
        color: #1e88e5;
    }
    .small-tab{
        font-size: 0.9rem;
    }
    hr{
        border: none;
        background-color: #2a3035;
        color: #2a3035;
        height: 1px;
    }
    .collapse-list ul li{
        color: #455a64;
        list-style-type: none;
        margin-left: -30px;
    }
    .toggle-btn ul li{
        list-style-type: none;
        margin-left: -30px;
    }
    .toggle-btn-line ul li{
        display: inline;
        list-style-type: none;
    }

</style>



