<template>
    <!--Блок парасемров документа на вкладке Основные-->
    <div class="col-11 bg-white ml-3">
        <div class="row mt-4 ">
            <div class="col-5 text-muted pl-2 pr-0">{{statusDoc.name}}</div>
            <div class="col-7 text-blue">{{statusDoc.description}}</div>
        </div>
        <div class="row mt-4 ">
            <div class="col-5 text-muted pl-2 pr-0">Рег номер:</div>
            <div class="col-7 text-blue">
                <input type="text"
                       class="form-control"
                       id="exampleInputNumber">
            </div>
        </div>
        <div class="row mt-4 ">
            <div class="col-5 text-muted pl-2 pr-0">Дата регистрации:</div>
            <div class="col-7 text-blue">
                <input type="date"
                       class="form-control"
                       id="exampleInputDate">
                <i class="far fa-calendar-alt date-field-r"></i>
            </div>
        </div>
        <div class="row mt-4 ">
            <div class="col-5 text-muted pl-2 pr-0">В ответ на:</div>
            <div class="col-7 text-blue mid-tab">
                <a href="#">Вх. Иа-2299 от 21.09.2018</a>
            </div>
        </div>
        <div class="row mt-4 ">
            <div class="col-12">
                <div class="form-group">
                    <label class="text-muted"
                           for="exampleFormControlShortDescription">
                        Краткое описание
                    </label>
                    <textarea class="form-control"
                              id="exampleFormControlShortDescription"
                              rows="5">
                    </textarea>
                </div>
            </div>
        </div>
        <div class="row mt-4 ">
            <div class="col-5 text-muted pl-2 pr-0">Адресат:</div>
            <div class="col-7 text-blue"></div>
        </div>
        <div class="row mt-4 ">
            <ol>
                <li v-for="(item , index) in statusDoc.adress" :key="index">
                    <a href="#" class="text-blue">{{item}}
                        <i class="fas fa-user-times fa-1x fr"></i>
                    </a>
                </li>
                <li><a href="#" class="text-blue">
                    <i class="fas fa-user-plus"></i>
                </a>
                </li>
            </ol>
        </div>
        <div class="row mt-4">
            <div class="col-2 text-muted pl-2 pr-0">Автор:</div>
            <div class="col-10 text-blue">
                <b>
                    <div class="row">
                        <div class="col-3">
                            <i class="far fa-user-circle fa-3x "></i>
                        </div>
                        <div class="col-9">
                            <span class="summary">{{statusDoc.author}}</span>
                            <br>
                            <span class="text-muted small-tab">{{statusDoc.authorStatus}}</span>
                        </div>
                    </div>
                </b>
            </div>
        </div>
        <div class="row mt-4 ">
            <div class="col-5 text-muted pl-2 pr-0">Дата создания:</div>
            <div class="col-7 text-blue">{{statusDoc.date}}</div>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'properti-document-tab',
    props: [
      'statusDoc'
    ]
  }
</script>

<style scoped>
    .text-blue{
        color: #1e88e5;
    }
    .small-tab{
        font-size: 0.9rem;
    }
    .mid-tab{
        font-size: 1rem;
    }
    hr{
        border: none;
        background-color: #2a3035;
        color: #2a3035;
        height: 1px;
    }
    .collapse-list ul li{
        color: #455a64;
        list-style-type: none;
        margin-left: -30px;
    }
    .toggle-btn ul li{
        list-style-type: none;
        margin-left: -30px;
    }
    .toggle-btn-line ul li{
        display: inline;
        list-style-type: none;
    }
    .date-field-r{
        position: absolute;
        float: right;
        right: 20px;
        top: 10px;
    }
    .fr{
        float: right;
        margin-left: 10px;
        margin-top: 5px;
    }

</style>



